import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
import { imgUrl } from '@/api/url'
export default {
  components: {
    Scroll
  },
  data() {
    return {
      imgUrl,
      dataList: [],
      fileId: ''
    }
  },
  created() {
    this.initDate()
  },
  methods: {
    initDate() {
      loadData('/article/menu_list', {
        type: 1
      }).then(data => {
        console.log(data)
        this.dataList = data.data
      })
    }
  }
}



